
.zc-shadow-layer {
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1001;
  position: fixed;
  background-color: rgba(99, 99, 99, 0.7);
}
.zc-shadow-layer .bootbox {
    width: 800px;
    margin: auto;
}
.zc-shadow-layer .bootbox .modal-dialog {
      position: relative;
      margin: 30px auto;
      width: 860px;
      max-height: 500px;
}
.zc-shadow-layer .bootbox .modal-dialog .modal-content {
        border-radius: 1px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
        position: relative;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid #999;
        outline: 0;
}
.zc-shadow-layer .bootbox .modal-dialog .modal-content .modal-header {
          height: 54px;
          border-bottom: 1px solid #38bfbe;
          background-color: #f4fdfb;
          padding: 18px 30px;
          min-height: 16.43px;
}
.zc-shadow-layer .bootbox .modal-dialog .modal-content .modal-header .close {
            margin: 0;
            line-height: 18px;
            color: #09b1af;
            opacity: 1;
            -webkit-appearance: none;
            padding: 0;
            cursor: pointer;
            background: 0 0;
            border: 0;
            float: right;
            font-size: 21px;
            font-weight: 700;
            float: right;
            font-size: 21px;
            font-weight: 700;
}
.zc-shadow-layer .bootbox .modal-dialog .modal-content .modal-header .modal-title {
            line-height: 16px;
            font-size: 16px;
            margin: 0;
            padding: 0;
            font-family: "microsoft yahei";
}
.zc-shadow-layer .bootbox .modal-dialog .modal-content .modal-header .modal-body {
            padding: 0;
            text-align: center;
            position: relative;
            min-height: 120px;
}



