
.left-navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  background: #fff;
  width: 291px;
  border-right: 1px solid #dee8e9;
}
.left-navigation .dropdown {
    width: 291px;
    height: 80px;
    background: #f2f7f7;
    position: relative;
    border-bottom: 1px solid #e0e8e8;
    z-index: 999;
}
.left-navigation .dropdown .user_menu {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      font-size: 16px;
      font-weight: 900;
      color: white;
      width: 180px;
      float: left;
      cursor: pointer;
      background: #f2f7f7;
      border-top: 2px solid #f2f7f7;
      padding: 20px 0 20px 20px;
}
.left-navigation .dropdown .user_menu a {
        float: right;
}
.left-navigation .dropdown .user_menu .menuDropdown {
        height: 40px;
        margin: 0;
        position: relative;
}
.left-navigation .dropdown .user_menu .menuDropdown .connection_style {
          position: absolute;
          left: 53px;
          bottom: 2px;
          font-size: 12px;
          font-family: 'Microsoft YaHei';
          color: #999;
}
.left-navigation .dropdown .user_menu .menuDropdown .current_user_avatar {
          width: 40px;
          border-radius: 50%;
          overflow: hidden;
          margin-right: 12px;
          float: left;
}
.left-navigation .dropdown .user_menu .menuDropdown .current_user_avatar .member_image {
            width: 40px;
            height: 40px;
}
.left-navigation .dropdown .user_menu .menuDropdown .current_user_name {
          color: #555556;
          font-size: 14px;
          font-family: 'Microsoft YaHei';
          position: absolute;
          display: inline-block;
          max-width: 100px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          left: 53px;
          top: 3px;
          line-height: 16px;
}
.left-navigation .dropdown .user_menu .menuDropdown .connection_icon {
          width: 14px;
          height: 14px;
          position: absolute;
          top: 30px;
          left: 30px;
}
.left-navigation .dropdown .user_menu .menuDropdown > .online {
          background-image: url("../assets/img/online-afd4b65201.png");
}
.left-navigation .dropdown .user_menu .menuDropdown > .busy {
          background-image: url("../assets/img/busy-993c8f0f60.png");
}
.left-navigation .dropdown .user_menu .menuDropdown > .offline {
          background-image: url("../assets/img/offline-d2a9280121.png");
}
.left-navigation .dropdown .user_menu .dropdownMenu-enter-active {
        -webkit-animation: dropdownMenu-in .2s;
                animation: dropdownMenu-in .2s;
}
.left-navigation .dropdown .user_menu .dropdownMenu-leave-active {
        -webkit-animation: dropdownMenu-out .2s;
                animation: dropdownMenu-out .2s;
}
@-webkit-keyframes dropdownMenu-in {
0% {
    height: 0;
}
50% {
    height: 50%;
}
100% {
    height: 100%;
}
}
@keyframes dropdownMenu-in {
0% {
    height: 0;
}
50% {
    height: 50%;
}
100% {
    height: 100%;
}
}
@-webkit-keyframes dropdownMenu-out {
0% {
    height: 100%;
}
50% {
    height: 50%;
}
100% {
    height: 0;
}
}
@keyframes dropdownMenu-out {
0% {
    height: 100%;
}
50% {
    height: 50%;
}
100% {
    height: 0;
}
}
.left-navigation .dropdown .user_menu .status-user {
        min-width: 66px;
        width: 66px;
        left: 52px;
        top: 68px;
        height: 92px;
        border: 1px solid #09aeb0;
        padding: 0;
        position: absolute;
        background-color: white;
        overflow: hidden;
        border-radius: 4px;
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
}
.left-navigation .dropdown .user_menu .status-user ul {
          list-style-type: none;
          padding: 0;
          margin: 0;
}
.left-navigation .dropdown .user_menu .status-user ul li {
            padding: 0;
            height: 30px;
            line-height: 30px;
            color: #555555;
            font-size: 12px;
            text-align: center;
            font-weight: 400;
            white-space: nowrap;
}
.left-navigation .dropdown .user_menu .status-user ul li .user-icon-busy {
              background: url("../assets/img/busy-993c8f0f60.png") no-repeat;
              padding: 1px 0px 1px 16px;
              font-size: 12px;
}
.left-navigation .dropdown .user_menu .status-user ul li .user-icon-online {
              background: url("../assets/img/online-afd4b65201.png") no-repeat;
              padding: 1px 0px 1px 16px;
              font-size: 12px;
}
.left-navigation .dropdown .user_menu .status-user ul li .user-icon-offine {
              background: url("../assets/img/offline-d2a9280121.png") no-repeat;
              padding: 1px 0px 1px 16px;
              font-size: 12px;
}
.left-navigation .dropdown .user_menu .status-user ul li:hover {
              background-color: #dbf3f3;
              color: #555555;
              font-size: 12px;
}
.left-navigation .dropdown .joinBox-yy {
      position: absolute;
      left: 182px;
      top: 27px;
      float: right;
}
.left-navigation .dropdown .joinBox-yy .waitnum {
        display: inline-block;
        cursor: pointer;
        font-size: 12px;
        color: #09aeb0;
        float: left;
        margin: 5px;
}
.left-navigation .dropdown .openOtherUser {
      font-size: 22px;
      color: #a4b2b3;
      text-decoration: none;
      float: left;
}
.left-navigation .dropdown .openOtherUser i {
        background: url("../assets/images/newChat2.png");
        width: 25px;
        height: 20px;
        background-position: -40px -110px;
        display: inline-block;
}
.left-navigation .dropdown .openOtherUser .i-hover {
        background: url("../assets/images/newChat2.png");
        width: 25px;
        height: 20px;
        background-position: -72px -110px;
        display: inline-block;
}
.left-navigation .dropdown .openOtherUser span {
        font-size: 12px;
        color: #09aeb0;
}
.left-navigation .dropdown .openOtherUser span:hover {
          color: #09c0c1;
}
.left-navigation .mainNav {
    background: #fff;
}
.left-navigation .mainNav li {
      background-color: #f2f7f7;
      position: relative;
}
.left-navigation .mainNav li .main_tab {
        border-bottom: 1px solid #ddd;
}
.left-navigation .mainNav li .main_tab li {
          clear: none;
          float: left;
}
.left-navigation .mainNav li .main_tab li a {
            display: block;
            font-size: 14px;
            width: 145px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            background-color: #f2f7f7;
            padding: 0;
            color: #565657;
            border: 0;
            font-weight: bold;
            text-decoration: none;
}
.left-navigation .mainNav li .main_tab li.active a {
            color: #09aeaf;
            background-color: #f2f7f7;
            border-bottom: solid 3px #09aeb0;
}
.left-navigation .tab-content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background-color: #f2f7f7;
    overflow: auto;
}
.left-navigation .tab-content .tab-pane {
      overflow-y: auto;
}
.left-navigation .tab-content .tab-pane .radio-box-yy {
        padding: 15px 16px;
        line-height: 18px;
        text-align: center;
        border-bottom: 1px solid #e0e8e8;
}
.left-navigation .tab-content .onoffTip {
      height: 30px;
      line-height: 30px;
      background: #f2f7f7;
      color: #96a2a3;
      padding-left: 20px;
      border-bottom: 1px solid #e0e8e8;
}
.left-navigation .tab-content li .item-detail {
      height: 100%;
      margin-left: 62px;
      padding-top: 12px;
      min-height: 68px;
}
.left-navigation .tab-content li .item-detail .unameWidely .uname {
        max-width: 130px;
        height: 20px;
        line-height: 20px;
        overflow: hidden;
        display: inline-block;
        vertical-align: bottom;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #555556;
        font-size: 14px;
        font-family: "Microsoft YaHei", serif;
}
.left-navigation .tab-content li .item-detail .chat-detail {
        color: #999;
        display: block;
        width: 130px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: 4px 0 8px 0;
        height: 20px;
        font-size: 13px;
}
.left-navigation .tab-content li .item-detail .chat-detail.orange {
          color: #ffad01;
}
.left-navigation .tab-content li.user-list-item {
      width: 100%;
      cursor: pointer;
      position: relative;
      background-color: #f2f7f7;
      border-bottom: 1px solid rgba(88, 89, 91, 0.1);
      overflow: hidden;
      transition: background 0.3s linear, border-width 0.1s linear;
}
.left-navigation .tab-content li.user-list-item:hover {
        background: #e6f0ef;
}
.left-navigation .tab-content li.user-list-item:hover .user-del {
          display: block;
}
.left-navigation .tab-content li.user-list-item .user-del {
        height: 20px;
        width: 20px;
        position: absolute;
        display: block;
        cursor: pointer;
        top: 0px;
        display: none;
        right: 0px;
        background: url(../assets/images/userListIco-5c38f007fc.png) 6px -128px no-repeat;
}
.left-navigation .tab-content li.user-list-item .icon {
        background-color: #ccc;
}
.left-navigation .tab-content li.user-list-item .icon.laptop {
          float: left;
          height: 40px;
          width: 40px;
          display: block;
          border-radius: 50%;
          position: relative;
          margin-top: 14px;
          margin-left: 14px;
          background: #60adf6 url("../assets/images/newChat1.png") -98px -235px;
}
.left-navigation .tab-content li.user-list-item .icon.laptop.offline {
            background-color: #bac2c1;
}
.left-navigation .tab-content li.user-list-item .icon.laptop .badge {
            border-radius: 10px;
            font-size: 12px;
            font-weight: 700;
            line-height: 1;
            color: #fff;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            position: absolute;
            visibility: hidden;
            right: -5px;
            font-family: "Microsoft YaHei";
            font-style: normal;
            top: -4px;
            padding-top: 2px;
}
.left-navigation .tab-content li.user-list-item .icon.laptop .badge.badge-red {
              background: #ff7878;
}
.left-navigation .tab-content li.user-list-item .receiveTime {
        position: absolute;
        top: 36px;
        right: 14px;
        font-size: 12px;
        font-family: "Microsoft Yahei", serif;
        color: #bbbfbf;
}
.left-navigation .tab-content li.active {
      background: #d8ebea;
      border-left: 3px solid #09aeb0;
}
.left-navigation .tab-content li.active .user-del {
        display: block;
}
.left-navigation .tab-content li.active:hover {
        background-color: #d8ebea;
}



