
@charset "UTF-8";
.bootbox-body .reqTableBox {
  font-family: ;
  padding: 52px 54px;
}
.bootbox-body .reqTableBox .reqBottom {
    margin-top: 15px;
}
.bootbox-body .reqTableBox .reqBottom p {
      font-size: 12px;
      font-weight: bold;
      color: #9c9c9c;
}
.bootbox-body .reqTableBox .reqBottom .reaPrevBtn, .bootbox-body .reqTableBox .reqBottom .reaNextBtn {
      display: block;
      float: left;
      width: 58px;
      height: 24px;
      line-height: 21px;
      font-size: 12px;
      border: 1px solid #e7ecf0;
      color: #09aeb0;
      font-weight: bold;
      font-family: ;
      margin: 0px 5px;
      text-align: center;
      text-decoration: none;
}
.bootbox-body .reqTableBox .reqBottom .reaPrevBtn:hover, .bootbox-body .reqTableBox .reqBottom .reaNextBtn:hover {
        color: #2a6496;
        text-decoration: underline;
}
.bootbox-body .reqTableBox .reqBottom .pageNumber {
      float: left;
      overflow: hidden;
}
.bootbox-body .reqTableBox .reqMenuBox {
    height: 34px;
    margin-bottom: 30px;
    border-bottom: 1px solid #e5e5e5;
}
.bootbox-body .reqTableBox .reqMenuBox .ls-light-green-btn {
      background-color: #eff5f5;
      width: 128px;
      height: 33px;
      transition: all 400ms linear;
      border: 1px solid #eff5f5;
      border-top-width: 3px;
      text-align: center;
      line-height: 33px;
      font-size: 14px;
      color: #999999;
      padding: 0;
      margin: 0;
      outline: none;
}
.bootbox-body .reqTableBox .reqMenuBox .ls-light-green-btn.active {
        height: 34px;
        line-height: 34px;
        border-left: 1px solid #e5e5e5;
        border-right: 1px solid #e5e5e5;
        border-top: 3px solid #09aeb0;
        border-bottom: 1px solid #fff;
        margin-bottom: -1px;
        background-color: #fff;
        color: #09adae;
}
.bootbox-body .reqTableBox .queueTableWrapper {
    height: 270px;
    overflow: hidden;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable {
      border-collapse: collapse;
      width: 100%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr th {
        font-size: 14px;
        font-weight: bold;
        color: #555556;
        text-align: left;
        background-color: #dbdbdb;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th2 {
        width: 20%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th3 {
        width: 13%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th4 {
        width: 14%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th5 {
        width: 21%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th6 {
        position: relative;
        z-index: 10;
        cursor: pointer;
        width: 20%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th6 span {
          position: absolute;
          left: 58px;
          top: 10px;
          z-index: 20;
          width: 17px;
          height: 17px;
          background: url(../../assets/images/investIco-9397e3e5bc.png) no-repeat -429px 0px;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable thead tr .th7 {
        width: 15%;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable tbody tr:nth-child(even) {
        background-color: #f6f6f6;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable tbody tr td {
        white-space: nowrap;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable tbody tr td button {
          border: 0;
          border-radius: 3px;
          font-size: 12px;
          cursor: pointer;
          padding: 6px;
          color: white;
          outline: none;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable tbody tr td button.invitation {
            background-color: #09aeaf;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable tbody tr td button.refuse {
            background-color: #d30000;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable tbody tr td button:hover {
            font-weight: 700;
}
.bootbox-body .reqTableBox .queueTableWrapper .queueTable td, .bootbox-body .reqTableBox .queueTableWrapper .queueTable th {
        padding: 5px;
}
.bootbox-body .reqTableBox .noUser {
    width: 100%;
    height: 260px;
}
.bootbox-body .reqTableBox .noUser p {
      width: 100%;
      height: 260px;
      line-height: 260px;
      text-align: center;
      font-size: 15px;
      color: #989898;
      font-family: ;
}
.bootbox-body .reqTableBox .screen-condition {
    margin-bottom: 30px;
    position: relative;
    z-index: 10;
}
.bootbox-body .reqTableBox .screen-condition .condition {
      display: inline-block;
      position: relative;
      float: left;
      width: 100px;
      height: 34px;
      line-height: 34px;
      font-size: 12px;
      color: #565656;
      padding-left: 10px;
      margin-right: 10px;
      border: 1px solid #ededed;
      cursor: pointer;
      text-align: left;
}
.bootbox-body .reqTableBox .screen-condition .condition .ico {
        position: absolute;
        top: 13px;
        left: 80px;
        width: 17px;
        height: 17px;
        background: url(../../assets/images/investIco-9397e3e5bc.png) no-repeat 3px 0px;
}
.bootbox-body .reqTableBox .screen-condition .conditionInput {
      float: left;
      width: 280px;
      height: 34px;
      border: 1px solid #ededed;
      color: #999999;
      font-size: 12px;
      padding-left: 35px;
      outline: none;
}
.bootbox-body .reqTableBox .screen-condition .magnifier {
      display: inline-block;
      width: 20px;
      height: 20px;
      position: absolute;
      top: 8px;
      left: 230px;
      background: url(../../assets/images/investIco-9397e3e5bc.png) no-repeat -173px 0px;
}
.bootbox-body .reqTableBox .reqRefNow {
    float: left;
    margin-top: 8px;
    font-size: 11px;
    color: #909090;
}
.bootbox-body .reqTableBox .reaRefBtn {
    display: inline-block;
    float: left;
    padding-left: 6px;
    margin-top: 8px;
    color: #06aeae;
    font-size: 13px;
}
.bootbox-body .reqTableBox .reaRefBtn .refresh {
      display: inline-block;
      float: left;
      width: 17px;
      height: 17px;
      margin: 2px;
      background: url(../../assets/images/investIco-9397e3e5bc.png) no-repeat -467px 0px;
}



