
.sideinfo {
  border-left: 1px solid #dee8e9;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
}
.sideinfo .tabmenu {
    height: 54px;
    background-color: #f2f7f7;
    border-bottom: 1px solid #e0e8e8;
}
.sideinfo .tabmenu li {
      float: left;
      width: 94px;
      cursor: pointer;
      font-size: 13px;
      color: #a4b2b3;
      padding-top: 8px;
      text-align: center;
}
.sideinfo .tabmenu li i.fa-user1 {
        background: url(../assets/images/newChat2.png) -20px -665px;
        width: 40px;
        height: 20px;
        display: inline-block;
}
.sideinfo .tabmenu li i.glyphicon-comment1 {
        background: url(../assets/images/newChat2.png) -65px -665px;
        width: 40px;
        height: 20px;
        display: inline-block;
}
.sideinfo .tabmenu li i.fa-comments-o1 {
        background: url(../assets/images/newChat2.png) -110px -665px;
        width: 40px;
        height: 20px;
        display: inline-block;
}
.sideinfo .tabmenu li.active {
        color: #09aeb0;
        border-bottom: solid 3px #09aeb0;
}
.sideinfo .tabmenu li.active .fa-user1 {
          background-position: -20px -713px;
}
.sideinfo .tabmenu li.active .glyphicon-comment1 {
          background-position: -65px -713px;
}
.sideinfo .tabmenu li.active .fa-comments-o1 {
          background-position: -110px -713px;
}
.sideinfo .tabcontent {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: auto;
    padding: 10px;
    clear: both;
}
.sideinfo .tabcontent li .nullcon .tab-profilebg {
      width: 90px;
      height: 65px;
      margin: 50% auto 0;
      display: block;
      background: url(../assets/images/noProfileUserInfo.png) no-repeat;
      background-position-x: -27px;
}
.sideinfo .tabcontent li dl {
      color: #4E5A75;
      font-size: 14px;
      line-height: 14px;
}
.sideinfo .tabcontent li dl dt {
        color: #565656;
        font-weight: 500;
        margin: 25px 0;
}
.sideinfo .tabcontent li dl dd {
        margin: 15px 0;
}
.sideinfo .tabcontent li .saveCustomerInfo {
      margin: auto;
      clear: both;
      float: left;
      width: 116px;
      height: 30px;
      font-size: 12px;
      color: #fff;
      text-align: center;
      line-height: 30px;
      background: #07afb0;
      border-radius: 2px;
      margin-top: 20px;
      cursor: pointer;
      margin-left: 62px;
}



